import axios from 'axios'
import { uuid } from 'uuidv4';

export default class UserApi  {
    
    static checkUserDetails(userRegData){
         console.log("Commming")
        return axios.get("http://localhost:3001/user")
            .then(res => this.checkUser(res.data,userRegData))
            .catch(err => console.log(err));
    }
    static checkUser(res,userRegData){
        let status ="";
        let flag=0;
        for(let user of res){
            flag=0;
            if(userRegData.email === user.email || userRegData.phone === user.phone ){
                status ="Email id or Phone number already exists";
                return status;
            }
            else{
                flag=1;
            }
        }
        if(flag===1)
           return this.addUserRegData(userRegData);
    }

    static addUserRegData(userRegData){
        console.log(userRegData);
        const body = { 
            id:uuid(),
            firstname:userRegData.firstname,
            lastname:userRegData.lastname,
            phone:userRegData.phone,
            email:userRegData.email,
            password:userRegData.password,
            location:userRegData.location,
            date:userRegData.date,
            time:userRegData.time
        }
        return axios.post('http://localhost:3001/user', body)
                    .then(respose => {return "Username added"})
    }


    static login(loginUserData){
        return axios.get('http://localhost:3001/user')
            .then(user => this.checkLoginData(user.data,loginUserData))
    }
    static checkLoginData(usersData,loginUserData) {
        let flag=0;
        for(let user of usersData){
            flag=0;
            if((user.email === loginUserData.email) & (user.password === loginUserData.password)){
                let status={
                    statusState:true,
                    statusMessage:"User Logged In"
                }
                console.log(status.statusMessage);
                return status;
            }
            else{
                flag=1;
            }
        }
        if(flag===1){
            let status={
                statusState:false,
                statusMessage: "Email and password may wrong. \n Enter Correct email and pasword"
            }
            return status;
        }
    }

    static getUser(username){
        return axios.get(`http://localhost:3001/user?q=${username}`)
        // .then(res =>  console.log(res.data))

    }
}

