
import axios from 'axios'

export default class ProductApi {
    static baseURL =`http://localhost:3001`;
    static basePath = `/product`
    static getProducts(){
        console.log("comming")
        return axios.get(`http://localhost:3001/product`)
    }
    static addProduct(body){
        console.log("In Prod Api")
        axios.post(`${this.baseURL+this.basePath}`,body)
            .then((res) => {return "Product Added Successfully"})
            .catch(err => {return err})
    }
    static searchProduct(search){
        console.log("Comming to search ethod 12")
        return axios.get(`${this.baseURL+this.basePath}?name_like=${search}`)
    }
    static deleteProduct(product){
        return axios.delete(`${this.baseURL+this.basePath}/${product.id}`)
        .then(res => this.getProducts())
    }
    static updateProduct = async(id,body)  => {
        return await axios.put(`${this.baseURL+this.basePath}/${id}`,body)
                .then(res => console.log("Data   "+ res.data))
    }

    static topViewedproducts = (views) =>{
        return axios.get(`${this.baseURL+this.basePath}?_sort=views&_order=desc&_limit=${views}`)
        // .then(res => console.log(res.data))
    }
}
