
import React from "react";
// import '../LoginRegister/sass/loginStyle.scss'
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import ProductApi from './data/ProductApi'
import {Select,MenuItem} from '@material-ui/core/';


const SignupSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, 'Too Short!')
    .max(50, 'Too Long!')
    .required('Required'),
  price: Yup.string()
    .min(1,' Enter minimum eight Characters')
    .required('Required'),
  quantity: Yup.string()
    .min(1,' Enter minimum eight Characters')
    .required('Required'),
  description: Yup.string()
    .min(2,' Enter minimum eight Characters')
    .required('Required'),
  // type: Yup.string()
  // .min(2,' Enter minimum eight Characters')
  // .required('Required'),
});



export default class AddProduct extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        name:"",
        price:"",
        quantity:"",
        description:"",
        type:"",
        status:"",
        date:"",
        time:"",
      };
    }
    render() {
      return (
        <div className="root-container ">
          <div className="box-container inner-container">
            <div className="header">
              Add Products
            </div>
            <div className="box">
              <Formik
                  initialValues={{
                      name:"",
                      price:"",
                      quantity:"",
                      description:"",
                  }}
                  validationSchema={SignupSchema}
                  onSubmit={values => {
                      console.log("Coming");
                      console.log(values);
                      this.setState({name:values.name,description:values.description,quantity:values.quantity,price:values.price});
                      console.log("Comming 2")
                      var date = new Date();
                      this.setState({
                          date: date.getDate() +"/"+ (date.toLocaleString('default', { month: 'short' })) +"/"+ date.getFullYear(),
                          time: date.getHours()+":"+ date.getMinutes()+":"+ date.getSeconds()
                      })
                      
                      let body ={
                          "name":this.state.name,
                          "description":this.state.description,
                          "price":this.state.price,
                          "quantity":this.state.quantity,
                          "type":this.state.type,
                          "date":this.state.date,
                          "time":this.state.time
                      }
                      
                        ProductApi.addProduct(body)
                            .then(res => {
                              this.setState({status:res});
                              console.log(res);
                            }
                        )
                    }
                  }
                  >
                  {({ errors, touched }) => (
                    <Form>
                      <div className="input-group">
                        <label htmlFor="name">Product Name</label>
                        <Field name="name" placeholder='Product Name' className="login-input" /> 
                        <small className="danger-error">{errors.name && touched.name ? (
                          <div>{errors.name}</div>
                          ) : null}
                        </small>
                      </div>
                      <div className="input-group">
                        <label htmlFor="quantity">Quantity</label>
                        <Field name="quantity" placeholder='Quantity' className="login-input" /> 
                        <small className="danger-error">{errors.quantity && touched.quantity ? (
                          <div>{errors.quantity}</div>
                          ) : null}
                        </small>
                      </div>
                      <div className="input-group">
                        <label htmlFor="price">Price</label>
                        <Field name="price" placeholder='Price' className="login-input" /> 
                        <small className="danger-error">{errors.price && touched.price ? (
                          <div>{errors.price}</div>
                          ) : null}
                        </small>
                      </div>
                      <div className="input-group">
                        <label htmlFor="description">Description</label>
                        <Field name="description" placeholder='Description' className="login-input" /> 
                        <small className="danger-error">{errors.description && touched.description ? (
                          <div>{errors.description}</div>
                          ) : null}
                        </small>
                      </div>
                      {/* <div className="input-group">
                        <label htmlFor="type">Type</label>
                        <Field name="type" placeholder='Type' className="login-input" /> 
                        <small className="danger-error">{errors.type && touched.type ? (
                          <div>{errors.type}</div>
                          ) : null}
                        </small>
                      </div> */}
                      <div className="input-group">
                        <label htmlFor="type">Type</label>
                        <Select
                            name="type"
                            type='drop-down'
                            // value={values.color}
                            onChange={(e)=> this.setState({type:e.target.value})}
                            // onBlur={handleBlur}
                            // style={{ display: 'block' }}
                          >
                            {/* <option value={""} label="Select a category" /> */}
                            <MenuItem value={""}>Select a category</MenuItem>
                            <MenuItem value={"Food"}>Food</MenuItem>
                            <MenuItem value={"Electronics"}>Electronics</MenuItem>
                            <MenuItem value={"Stationery"}>Stationery</MenuItem>
                            {/* <option value="" label="Select a category" />
                            <option value="Electronics" label="Electronics" />
                            <option value="Food" label="Food" />
                            <option value="Stationery" label="Stationery" /> */}
                          </Select>
                          <small className="danger-error">
                            {errors.type && touched.type &&
                            <div className="input-feedback">
                              {errors.type}
                            </div>}
                          </small>
                      </div>
                      {this.state.status}
                      <button type='submit' className="login-btn">Add Product</button>
                    </Form>
                  )}
                </Formik>
            </div>
          </div>
        </div>
      );
    }
}
