import React, { Component } from 'react';
import ProductApi from './data/ProductApi'
import Checkbox from '@material-ui/core/Checkbox';
import './Products.css'
import {Link,NavLink, Redirect} from 'react-router-dom'
import UpdateForm from './UpdateForm'
import Loginapp from '../LoginRegister/Loginapp';
import {Route,withRouter} from 'react-router-dom'
import {Grid,Paper} from '@material-ui/core'
// import createHistory from 'history/createBrowserHistory'
import TopViewedProducts from './TopViewedProducts'
import ViewProductDetails from './ViewProductdetails';
// export const EmpContext = React.createContext();




class ProductList extends Component {
    constructor(props){
        super(props);
        this.state={
            clicked:false,
            productList:[],
            search:"",
            id:true,
            name:true,
            price:true,
            des:true,
            updatedDate:true,
            addedDate:true,
            qty:true,
            type:true,
            updateProduct:[],
            updateComponent:false,
            isLoggedIn:this.props.statedata.isLoggedIn,
            proddetails:[]
        }
    }
    componentDidMount(){
        this.getProducts();
    }
    getProducts = () => {
        ProductApi.getProducts()
        .then(res => this.setState({productList:res.data}))
        .catch(err => console.log(err))

    }
    search = (e) => {
        if(this.state.search==="")
            ProductApi.getProducts().then(res => this.setState({productList:res.data}))
        else
            ProductApi.searchProduct(this.state.search).
            then(res =>{
                if(!(res ===''))
                    this.setState({productList:res.data})
                else
                    this.setState({productList:res.data})
            })
    }
    delete = async(product) =>{
        // this.setState({isLoggedin:this.props.statedata.isLoggedIn})
        await this.reroute();
        if(this.props.statedata.isLoggedIn)
            ProductApi.deleteProduct(product).then(res =>  this.setState({productList:res.data}));
        else
            this.props.history.push('login')
    }
    update = async (product) => {
        await this.reroute();
        setTimeout(() => {
            if(this.props.statedata.isLoggedIn)
            {
                console.log("Inside if "+this.state.isLoggedIn)
                this.setState({updateProduct:product,updateComponent:true})
                setTimeout(() => {
                    // this.getProducts();
                },100)
                console.log("UPDTAE PRODUCT DATA")
                console.log(this.state.updateProduct)
            }
            else
                this.props.history.push('/login')
        }, 1000);
    }
    // viewDetails = async(value) => {
    //     await this.reroute();
    //     if(this.props.statedata.isLoggedIn){
    //         // (<><ViewProductDetails productData={value}/></>)
    //         // this.props.proddata(value);
            // <NavLink to={`/product/${value.name}`}>{value}</NavLink>
    //         // this.setState({proddetails:value})
    //     }
    // }
    reroute = () => {
        this.setState({isLoggedin:this.props.statedata.isLoggedIn,updateComponent:false})
    }
    render() {
        let cardSize={
            width: "20rem",
        }
        return (
            <div className='container'>
            <div className='root-container'>
                <div>
                    <NavLink activeClassName="active"  to={'/addproduct'} ><button className="btn btn-success">Add Products Page </button></NavLink><br/>
                    <input type="text" placeholder ="Product Name" className=""
                        onChange={
                            e => {
                                this.setState({search:e.target.value});
                                setTimeout(()=>{ this.search(e)},500)}}
                    />
                </div>
                <TopViewedProducts/>
                {/* <Grid container className=" bg-light" spacing={5}>
                    <Grid item xs={9}>
                        <NavLink activeClassName="active"  to={'/addproduct'} ><button className="btn btn-success">Add Products Page </button></NavLink><br/>
                        <input type="text" placeholder ="Product Name" className=""
                            onChange={
                                e => {
                                    this.setState({search:e.target.value});
                                    setTimeout(()=>{ this.search(e)},500)}}
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <NavLink activeClassName="active"  to={'/addproduct'} ><button className="btn btn-success">Add Products Page </button></NavLink><br/>
                        <input type="text" placeholder ="Product Name" className=""
                            onChange={
                                e => {
                                    this.setState({search:e.target.value});
                                    setTimeout(()=>{ this.search(e)},500)}}
                        />
                        
                    </Grid>
                </Grid> */}
                
                <div className='label'>
                    <div >
                        <label className="label-heading">Customize your table values</label>
                    </div>
                    <label>
                        <Checkbox checked={this.state.id} onChange={ () => {this.setState({id:!this.state.id})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        ID
                    </label>
                    <label>
                        <Checkbox checked={this.state.type} onChange={ () => {this.setState({type:!this.state.type})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        TYPE
                    </label>
                    <label>
                        <Checkbox disabled checked inputProps={{ 'aria-label': 'disabled checked checkbox' }} />
                        Name
                    </label>
                    <label>
                        <Checkbox checked={this.state.price} onChange={() => {this.setState({price:!this.state.price})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Price
                    </label>
                    
                    <label>
                        <Checkbox checked={this.state.updatedDate} onChange={() => {this.setState({updatedDate:!this.state.updatedDate})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Recent updated Date
                    </label>
                    <label>
                        <Checkbox checked={this.state.addedDate} onChange={() => {this.setState({addedDate:!this.state.addedDate})}} 
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Added Date 
                    </label>
                    <label>
                        <Checkbox checked={this.state.des} onChange={ () => {this.setState({des:!this.state.des})}} 
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Description
                    </label>
                    <label>
                        <Checkbox checked={this.state.qty} onChange={() => {this.setState({qty:!this.state.qty})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Available Quantity
                    </label>
                </div>
                </div>
                <div className="row">
                    {this.state.productList.map((value,index) => {
                        return(
                            <div key={index} className="col-sm-4">
                                <div className="card" style={cardSize} >
                                    {/* <img class="card-img-top" src="..." alt="Card image cap"> */}
                                    <div className="card-body">
                                        <h5 className="card-title">{value.name}</h5>
                                        < >{this.state.id && (<><label >ID : {value.id}</label><br></br></>)}</>
                                        <>{this.state.name && (<><label>Name : {value.name}</label> <br></br></>)}</>
                                        <>{this.state.des && (<><label>Description : {value.description}</label> <br></br></>)}</>
                                        < >{ this.state.price && (<><label>Price : {value.price}</label> <br></br></>)}</>
                                        < >{this.state.qty && (<><label>Quantity : {value.quantity}</label> <br></br></>)}</>
                                        < >{this.state.type && (<><label>Type : {value.type}</label> <br></br></>)}</>
                                        < >{this.state.addedDate && (<><label>Date : {value.date}</label> <br></br></>)}</>
                                        < >{this.state.addedDate && (<><label>Date : {value.updatedDate}</label> <br></br></>)}</>
                                        < >{this.state.addedDate && (<><label>Date : {value.time}</label> <br></br></>)}</>
                                        < >{this.state.addedDate && (<><label>Date : {value.updatedTime}</label> <br></br></>)}</>
                                        < >{this.state.addedDate && (<><label>Date : {value.views}</label> <br></br></>)}</>
                                        {/* <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</> */}
                                        {/* <a href="#" class="btn btn-primary">Go somewhere</a> */}
                                    </div>
                                    <button className="btn btn-secondary" ><Link style={{"text-decoration":"none","color":"white"}} to={`/product/${value.name}`}>View {value.name} Details</Link></button>
                                    <button className="btn btn-primary" onClick={() => {this.update(value)}}>UPDATE</button>
                                    <button className="btn btn-danger" onClick={()=> this.delete(value)}>DELETE</button>
                                </div>
                            </div>
                        )
                    })}
                    {
                        this.state.updateComponent ? <UpdateForm product={this.state.updateProduct} closePopup={() => this.setState({updateComponent:false})}/> : null 
                        // (this.state.isLoggedIn && this.state.updateComponent) ? <UpdateForm product={this.state.updateProduct} closePopup={this.update}/> : null
                    }
                </div>
            </div>
        );
    }
}

export default withRouter(ProductList);