import ProductApi from './data/ProductApi';
// import { red } from '@material-ui/core/colors';


export const TopViewedProducts = () =>  {
    // constructor(props){
    //     super(props);
    //     this.state={
    //         topViewed:3,
    //         productsList:[]
    //     }
    // }
    
    const [topViewed, setTopViewed] = useState(3);
    const [productsList, setProductsList] = useState([]);
    
    const [Productdata, setProductData]= useState([])
    let TOP_VIEWED_PRODUCT_NAME = new Array;
    let TOP_VIEWS = new Array;
    let sampledata = {};
    const [data, setData] = useState(sampledata)
    useEffect(() => {
         ProductApi.topViewedproducts(topViewed).then(res => {setProductsList(res.data); 
            console.log("TOP Views cont  "+topViewed)
            setTimeout(() => {
                let i=0;
                for( ;i<topViewed;i++)
                {
                    TOP_VIEWED_PRODUCT_NAME.push(res.data[i].name);
                    TOP_VIEWS.push(res.data[i].views);
                }
                setProductData(TOP_VIEWED_PRODUCT_NAME)
                console.log("TOP VIEWS"+TOP_VIEWED_PRODUCT_NAME + "  "+TOP_VIEWS)
                sampledata = {
                    labels: TOP_VIEWED_PRODUCT_NAME,
                    datasets:[
                        {
                            label: "VIEWS",
                            backgroundColor: "purple",
                            data: TOP_VIEWS
                        }]
                    
                }
                // const [data, setData] = useState(sampledata)
                setData(sampledata)
            }, 1000);
        });
        
    },[topViewed])
    
    console.log("DATA");console.log(data)
    // const {
    //     data,
    //     primaryAxisShow,
    //     secondaryAxisShow,
    //     randomizeData,
    //     Options
    //   } = useChartConfig({
    //     series: 10,
    //     show: ['primaryAxisShow', 'secondaryAxisShow']
    //   })
    //   const axes = React.useMemo(
    //     () => [
    //       {
    //         primary: true,
    //         position: 'bottom',
    //         type: 'time',
    //         show: primaryAxisShow
    //       },
    //       { position: 'left', type: 'linear', show: secondaryAxisShow }
    //     ],
    //     [primaryAxisShow, secondaryAxisShow]
    //   )
    return (
        <div>
            <FormControl >
                <InputLabel id="demo-customized-select-label">Top Views</InputLabel>
                <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select"
                    value={topViewed}
                    onChange={(e) => setTopViewed(e.target.value)}
                    // input={<BootstrapInput />}
                >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                    <MenuItem value={10}>10</MenuItem>
                </Select>
                <div>
                    {/* <Chart  axes={axes} tooltip /> */}
                    <div style={{position:"relative",width:300,heiht:250}}>
                        <Line options={{responsive:false}} data={data} />
                    </div>
                </div>
                <div>
                    {
                        productsList.map((value,index) => {
                            return(
                                <ol key={index}>
                                    <li>{value.name}</li>{value.views}
                                </ol>
                            )}
                        )
                    }
                </div>
            </FormControl> 
    </div>
    );
//   }
}
