import React, { Component } from 'react';
import ProductApi from './data/ProductApi'
import {withRouter} from 'react-router-dom'
import'./Products.css'
import Image from '/home/punk/Music/capstone/src/download.png'
// import Image from 'data:image/png;base64,'
 class ViewProductDetails extends Component {
  constructor(props){
    super(props);
    this.state={
      id:"",
      name:"",
      type:"",
      price:"",
      description:"",
      quantity:"",
      date:"",
      updatedDate:"",
      time:"",
      updatedTime:"",
    }
  }
  componentDidMount(){
    console.log("test", localStorage.getItem('isloggedin'))
    console.log("false")
    console.log(localStorage.getItem('isloggedin'))
    
    if(!JSON.parse(localStorage.getItem('isloggedin')))
       return  this.props.history.push('/login')
    else{
        return this.setProducts();
    }
  }
  setProducts = async() => {
      await ProductApi.searchProduct(this.props.match.params.name).then( res => { this.setState({
          id : res.data[0].id,
          name : res.data[0].name,
          type : res.data[0].type,
          price : res.data[0].price,
          description : res.data[0].description,
          quantity : res.data[0].quantity,
          date : res.data[0].date,
          updatedDate : res.data[0].updatedDate,
          time : res.data[0].time,
          updatedTime : res.data[0].updatedTime,
          views: res.data[0].views
      })
      const body ={
        "name": this.state.name,
        "description": this.state.description,
        "price": this.state.price,
        "quantity":this.state.quantity,
        "type": this.state.type,
        "date": this.state.date,
        "time": this.state.time,
        "id": this.state.id,
        "updatedDate":"",
        "updatedTime" : "",
        "views":(this.state.views+1)
      }
      ProductApi.updateProduct(this.state.id,body).then(res => console.log(res))
    })
  }
  render() {
    return (
      <div className="containers">
          {/* <div className="box-left-containers"> */}
              <img src={Image} alt="Imgae not uploaded"></img>
              <div className="content">
                  <label className="prod">Name:</label>
                  <span>{this.state.name}</span><br></br>
                  <span>{this.state.id}</span><br></br>
                  <span>{this.state.price}</span><br></br>
                  <span>{this.state.description}</span><br></br>
                  <span>{this.state.quantity}</span><br></br>
                  <span>{this.state.date}</span><br></br>
                  <span>{this.state.time}</span><br></br>
                  <span>{this.state.updatedDate}</span><br></br>
                  <span>{this.state.updatedTime}</span><br></br>
                  <span>{this.state.views}</span><br></br>
              </div>
          {/* </div> */}
      </div>
    );
  }
}
export default withRouter(ViewProductDetails)